export default {
	data() {
		return {
			upload_cache: [],
			upload_cache_list: [],
			upload_before_list:[]
		}
	},
	name: 'sunui-upimg',
	props: {
		// 服务器url
		url: {
			type: String,
			default: 'http://www.luominus.com/api/v1/Index/uploadImg'
		},
		// 上传样式宽高
		upload_img_wh: {
			type: String,
			default: 'width:486rpx;height:486rpx;margin:0 auto;'
		},
		// 上传数量
		upload_count: {
			type: [Number, String],
			default: 1
		},
		// 是否自动上传? 可以先用变量为false然后再改为true即为手动上传
		upload_auto: {
			type: Boolean,
			default: true
		},
		// 是否显示删除
		upimg_move: {
			type: Boolean,
			default: true
		}
	},
	async created() {
		let _self = this;
		setTimeout(function() {
			_self.upload_before_list.map(function(item) {
				_self.upload_cache_list.push(item.path);
			});
			_self.emit();
		}, 300);
	},
	methods: {
		upImage(paths) {
			let _self = this;
			const promises = paths.map(function(path) {
				return promisify(upload)({
					url: _self.url,
					path: path,
					name: 'file',
					extra: {},
					_self: _self
				})
			});

			uni.showLoading({
				title: `正在上传...`,
			});

			Promise.all(promises).then(function(data) {
				uni.hideLoading();
				_self.upload_cache_list.push(...data);
				_self.emit();
			}).catch(function(res) {
				uni.hideLoading();
			});
		},
		chooseImage() {
			let _self = this;
			uni.chooseImage({
				count: _self.upload_count - _self.upload_before_list.length,
				sizeType: ['compressed', 'original'],
				sourceType: ['album', 'camera'],
				success:  function(res) {
					for (let i = 0, len = res.tempFiles.length; i < len; i++) {
						res.tempFiles[i]['upload_percent'] = 0;
						 _self.upload_before_list.push(res.tempFiles[i]);
					}
					_self.upload_cache =  res.tempFilePaths;
					_self.upload(_self.upload_auto);
				},
				fail: function(err) {
					console.log(err);
				}
			});
		},
		async upload(upload_auto) {
			let _self = this;
			upload_auto ? await _self.upImage(_self.upload_cache) : console.warn(
				`传输参数:this.$refs.xx.upload(true)才可上传,默认false`);
		},
		previewImage(idx) {
			let _self = this;
			let preview = [];
			for (let i = 0, len = _self.upload_before_list.length; i < len; i++) {
				preview.push(_self.upload_before_list[i].path);
			}
			uni.previewImage({
				current: idx,
				urls: preview
			});
		},
		removeImage(idx) {
			let _self = this;
			_self.upload_before_list.splice(idx, 1);
			_self.upload_cache_list.splice(idx, 1);
			_self.emit();
		},
		emit() {
			let _self = this;
			_self.$emit('change', _self.upload_cache_list);
		}
	}
}


const promisify = (api) => {
	return function(options, ...params) {
		return new Promise(function(resolve, reject) {
			api(Object.assign({}, options, {
				success: resolve,
				fail: reject
			}), ...params);
		});

	}
}

const upload = function(options) {
	let url = options.url,
		_self = options._self,
		path = options.path,
		name = options.name,
		// data = options.data,
		extra = options.extra,
		success = options.success,
		progress = options.progress,
		fail = options.fail

	const uploadTask = uni.uploadFile({
		url: url,
		filePath: path,
		name: name,
		formData: extra,
		success: function(res) {
			console.log(res)
			var data = res.data
			try {
				//Tip : 切记->主要修改这里图片的返回值为真实返回路径!!! 详情见示例
				data = JSON.parse(res.data).info;
			} catch (e) {
				throw (e, data)
			}
			// 根据自己的返回数据做相应判断,服务器返回200即代表成功请求
			if (res.statusCode == 200) {
				if (success) {
					success(data)
				}
			} else {
				if (fail) {
					fail(data)
				}
			}

		},
		fail: function(res) {
			console.log(res)
			if (fail) {
				fail(res)
			}
		}
	})
	uploadTask.onProgressUpdate(async function(res) {
		for (let i = 0, len = _self.upload_before_list.length; i < len; i++) {
			_self.upload_before_list[i]['upload_percent'] = await res.progress;
		}
		_self.upload_before_list = _self.upload_before_list;
	});
}
